'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { products, formatCurrencyPrecise } from '@/data/mockData';

interface OrderItem {
  productId: string;
  quantity: number;
}

export default function NewOrderPage() {
  const router = useRouter();
  const [channel, setChannel] = useState<'shopify' | 'amazon_fbm' | 'wholesale'>('shopify');
  const [customerName, setCustomerName] = useState('');
  const [customerEmail, setCustomerEmail] = useState('');
  const [addressLine1, setAddressLine1] = useState('');
  const [city, setCity] = useState('');
  const [state, setState] = useState('');
  const [postalCode, setPostalCode] = useState('');
  const [orderItems, setOrderItems] = useState<OrderItem[]>([{ productId: '', quantity: 1 }]);

  const nutrientProducts = products.filter(p => p.category === 'nutrient' && p.isActive);

  const addItem = () => {
    setOrderItems([...orderItems, { productId: '', quantity: 1 }]);
  };

  const removeItem = (index: number) => {
    setOrderItems(orderItems.filter((_, i) => i !== index));
  };

  const updateItem = (index: number, field: 'productId' | 'quantity', value: string | number) => {
    const newItems = [...orderItems];
    newItems[index] = { ...newItems[index], [field]: value };
    setOrderItems(newItems);
  };

  const calculateSubtotal = () => {
    return orderItems.reduce((sum, item) => {
      if (!item.productId) return sum;
      const product = products.find(p => p.id === item.productId);
      if (!product) return sum;
      const price = channel === 'shopify' ? product.prices.shopify :
                    channel === 'amazon_fbm' ? product.prices.amazon :
                    product.prices.wholesale;
      return sum + (price * item.quantity);
    }, 0);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // In a real app, this would create the order via API
    alert('Order created! (Demo only - no actual order created)');
    router.push('/orders');
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={() => router.back()}
            className="p-2 hover:bg-slate-800 rounded-lg transition-colors"
          >
            <i className="fas fa-arrow-left text-slate-400"></i>
          </button>
          <div>
            <h1 className="text-2xl font-bold text-white">Create Order</h1>
            <p className="text-sm text-slate-400">Manually create a new order</p>
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit} className="grid grid-cols-3 gap-6">
        {/* Main Content */}
        <div className="col-span-2 space-y-6">
          {/* Channel Selection */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
            <h2 className="font-semibold text-white mb-4">Sales Channel</h2>
            <div className="grid grid-cols-3 gap-3">
              <button
                type="button"
                onClick={() => setChannel('shopify')}
                className={`p-4 rounded-lg border transition-colors ${
                  channel === 'shopify'
                    ? 'bg-green-500/10 border-green-500/30 text-green-400'
                    : 'bg-slate-800/50 border-slate-700 text-slate-400 hover:border-slate-600'
                }`}
              >
                <i className="fab fa-shopify text-2xl mb-2"></i>
                <div className="text-sm font-medium">Shopify</div>
              </button>
              <button
                type="button"
                onClick={() => setChannel('amazon_fbm')}
                className={`p-4 rounded-lg border transition-colors ${
                  channel === 'amazon_fbm'
                    ? 'bg-orange-500/10 border-orange-500/30 text-orange-400'
                    : 'bg-slate-800/50 border-slate-700 text-slate-400 hover:border-slate-600'
                }`}
              >
                <i className="fab fa-amazon text-2xl mb-2"></i>
                <div className="text-sm font-medium">Amazon FBM</div>
              </button>
              <button
                type="button"
                onClick={() => setChannel('wholesale')}
                className={`p-4 rounded-lg border transition-colors ${
                  channel === 'wholesale'
                    ? 'bg-blue-500/10 border-blue-500/30 text-blue-400'
                    : 'bg-slate-800/50 border-slate-700 text-slate-400 hover:border-slate-600'
                }`}
              >
                <i className="fas fa-store text-2xl mb-2"></i>
                <div className="text-sm font-medium">Wholesale</div>
              </button>
            </div>
          </div>

          {/* Order Items */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
            <div className="flex items-center justify-between mb-4">
              <h2 className="font-semibold text-white">Order Items</h2>
              <button
                type="button"
                onClick={addItem}
                className="px-3 py-1.5 bg-emerald-600 hover:bg-emerald-500 text-white text-sm rounded-lg transition-colors"
              >
                <i className="fas fa-plus mr-2"></i>Add Item
              </button>
            </div>
            <div className="space-y-3">
              {orderItems.map((item, index) => {
                const selectedProduct = products.find(p => p.id === item.productId);
                const price = selectedProduct ? (
                  channel === 'shopify' ? selectedProduct.prices.shopify :
                  channel === 'amazon_fbm' ? selectedProduct.prices.amazon :
                  selectedProduct.prices.wholesale
                ) : 0;

                return (
                  <div key={index} className="flex items-center gap-3 p-3 bg-slate-900/50 rounded-lg">
                    <div className="flex-1">
                      <select
                        value={item.productId}
                        onChange={(e) => updateItem(index, 'productId', e.target.value)}
                        className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white focus:outline-none focus:border-emerald-500/50"
                      >
                        <option value="">Select Product...</option>
                        {nutrientProducts.map(product => (
                          <option key={product.id} value={product.id}>
                            {product.name} - {product.sku}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="w-24">
                      <input
                        type="number"
                        min="1"
                        value={item.quantity}
                        onChange={(e) => updateItem(index, 'quantity', parseInt(e.target.value) || 1)}
                        className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white text-center focus:outline-none focus:border-emerald-500/50"
                      />
                    </div>
                    <div className="w-24 text-right text-white">
                      {formatCurrencyPrecise(price * item.quantity)}
                    </div>
                    {orderItems.length > 1 && (
                      <button
                        type="button"
                        onClick={() => removeItem(index)}
                        className="p-2 text-slate-400 hover:text-red-400 transition-colors"
                      >
                        <i className="fas fa-trash"></i>
                      </button>
                    )}
                  </div>
                );
              })}
            </div>
          </div>

          {/* Customer Information */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
            <h2 className="font-semibold text-white mb-4">Customer Information</h2>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm text-slate-400 mb-2">Customer Name *</label>
                <input
                  type="text"
                  required
                  value={customerName}
                  onChange={(e) => setCustomerName(e.target.value)}
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                  placeholder="John Doe"
                />
              </div>
              <div>
                <label className="block text-sm text-slate-400 mb-2">Email</label>
                <input
                  type="email"
                  value={customerEmail}
                  onChange={(e) => setCustomerEmail(e.target.value)}
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                  placeholder="john@example.com"
                />
              </div>
            </div>
          </div>

          {/* Shipping Address */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
            <h2 className="font-semibold text-white mb-4">Shipping Address</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm text-slate-400 mb-2">Address Line 1 *</label>
                <input
                  type="text"
                  required
                  value={addressLine1}
                  onChange={(e) => setAddressLine1(e.target.value)}
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                  placeholder="123 Main St"
                />
              </div>
              <div className="grid grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm text-slate-400 mb-2">City *</label>
                  <input
                    type="text"
                    required
                    value={city}
                    onChange={(e) => setCity(e.target.value)}
                    className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                    placeholder="Houston"
                  />
                </div>
                <div>
                  <label className="block text-sm text-slate-400 mb-2">State *</label>
                  <input
                    type="text"
                    required
                    value={state}
                    onChange={(e) => setState(e.target.value)}
                    className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                    placeholder="TX"
                  />
                </div>
                <div>
                  <label className="block text-sm text-slate-400 mb-2">Postal Code *</label>
                  <input
                    type="text"
                    required
                    value={postalCode}
                    onChange={(e) => setPostalCode(e.target.value)}
                    className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50"
                    placeholder="77001"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Order Summary */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
            <div className="px-5 py-4 border-b border-slate-700/50">
              <h2 className="font-semibold text-white">Order Summary</h2>
            </div>
            <div className="p-5 space-y-4">
              <div className="flex items-center justify-between text-sm">
                <span className="text-slate-400">Subtotal</span>
                <span className="text-white">{formatCurrencyPrecise(calculateSubtotal())}</span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-slate-400">Shipping</span>
                <span className="text-slate-400">Calculated at checkout</span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-slate-400">Tax</span>
                <span className="text-slate-400">Calculated at checkout</span>
              </div>
              <div className="border-t border-slate-700/50 pt-4">
                <div className="flex items-center justify-between">
                  <span className="text-white font-medium">Estimated Total</span>
                  <span className="text-white font-bold text-lg">{formatCurrencyPrecise(calculateSubtotal())}</span>
                </div>
              </div>
            </div>
          </div>

          {/* Actions */}
          <div className="space-y-3">
            <button
              type="submit"
              className="w-full px-4 py-3 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors font-medium"
            >
              <i className="fas fa-check mr-2"></i>
              Create Order
            </button>
            <Link
              href="/orders"
              className="w-full px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors text-center block"
            >
              Cancel
            </Link>
          </div>

          {/* Notes */}
          <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
            <h2 className="font-semibold text-white mb-3">Order Notes</h2>
            <textarea
              rows={4}
              className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2.5 text-white placeholder-slate-500 focus:outline-none focus:border-emerald-500/50 resize-none"
              placeholder="Add any notes about this order..."
            ></textarea>
          </div>
        </div>
      </form>
    </div>
  );
}
